.timeline-wrapper i{font-family:IBM Plex Mono;font-style:initial}.timeline-item{display:flex;justify-content:flex-start;align-items:center;position:relative;padding-top:1.2rem;padding-bottom:1.2rem;min-height:7rem;letter-spacing:.04rem}@media(min-width: 1025px){.timeline-item::before{content:"";position:absolute;z-index:1;top:0;bottom:calc(50% + 2.5rem);left:calc(2rem - .5px);border-left:3px dashed rgba(255,255,255,.3)}.timeline-item::after{content:"";position:absolute;z-index:1;top:calc(50% + 2.5rem);bottom:0;left:calc(2rem - .5px);border-left:3px dashed rgba(255,255,255,.3)}.timeline-item:last-of-type::after,.timeline-item:first-of-type::before{border-left:none}}.item-icon{min-width:4rem;height:4rem;margin-right:2rem;z-index:10}.item-title{font-size:1.2rem;font-weight:500;letter-spacing:.025rem;color:#ff8900}.item-date{font-size:.9rem;margin-bottom:.5rem;color:rgba(255,255,255,.65)}.item-body{padding-left:1rem}.item-body>li::before{content:"â€¢";color:rgba(134,181,4,.9);font-weight:bold;display:inline-block;width:1rem;margin-left:-1rem}.item-body>li:not(:last-child){margin-bottom:.3rem}.download{color:#86b504;padding-left:1em;padding-right:1rem;padding-top:.3rem;padding-bottom:.3rem;border:.2rem solid #86b504;font-size:1.2rem;letter-spacing:.04rem;font-weight:500;text-transform:uppercase;transition:box-shadow,background .2s linear}@media(max-width: 1024px){.download{margin-top:2rem}}.download:hover{background:rgba(134,181,4,.35)}.animating{animation:pulseOut .5s;box-shadow:0 0 0 1.5em rgba(0,0,0,0)}@media(max-width: 1024px){.timeline-item{flex-direction:column}.item-icon{margin-right:0}.timeline-wrapper [class^=item-]{text-align:center}}@keyframes pulseOut{0%{box-shadow:0 0 0 0 #86b504}}